"use client";

import { ListPage } from "@/components/list-page";
import { apiDelete, apiGet } from "@/lib/api";
import { useState } from "react";

type ContractRow = {
  id: number;
  number: string;
  conclusionDate: string;
  city: string;
  organization: string;
  representative: string;
  representativePosition: string;
  basis: string;
  employee: string;
  employeePassport: string;
  department: string;
  position: string;
  place: string;
  personnelNumber: string;
  workDateFrom: string;
  workDateTo: string;
  salary: string;
  conditionsClass: string;
  probationMonths: string;
  paidLeaveDays: string;
  workHoursFrom: string;
  workHoursTo: string;
  breakFrom: string;
  breakTo: string;
};

export default function ContractsClient({ initialRows }: { initialRows: ContractRow[] }) {
  const [rows, setRows] = useState<ContractRow[]>(initialRows ?? []);
  type Org = { id: number; title?: string; address?: string; inn?: string; kpp?: string; okpo?: string; account_id?: number };
  type Individual = {
    id: number;
    last_name?: string;
    first_name?: string;
    patronymic?: string;
    passport_series?: string;
    passport_number?: string;
    passport_issued_by?: string;
    passport_issued_at?: string;
    passport_division_code?: string;
    registration_address?: string;
  };
  type Account = { id: number; number?: string; bank?: string; bic?: string };

  const asIso = (value: any) => {
    if (value === undefined || value === null || value === "") return "—";
    if (typeof value === "string" && value.includes("-")) return value;
    const num = typeof value === "number" ? value : (typeof value === "string" && /^\d+$/.test(value) ? Number(value) : NaN);
    if (Number.isNaN(num)) return String(value);
    const ms = num > 1e12 ? num : num * 1000;
    const d = new Date(ms);
    return Number.isNaN(d.getTime()) ? String(value) : d.toISOString().slice(0, 10);
  };

  const asHumanDate = (value: any) => {
    const iso = asIso(value);
    if (iso === "—") return iso;
    const d = new Date(iso);
    return Number.isNaN(d.getTime())
      ? iso
      : d.toLocaleDateString("ru-RU", { day: "2-digit", month: "long", year: "numeric" }).replace(" г.", "") + " г.";
  };

  const initials = (fullName?: string) => {
    if (!fullName) return "";
    const parts = fullName.trim().split(/\s+/);
    if (!parts.length) return "";
    const [last, first, middle] = parts;
    const f = first ? `${first[0]}.` : "";
    const m = middle ? `${middle[0]}.` : "";
    return [last, f, m].filter(Boolean).join(" ");
  };

  const buildPrintHtml = (data: {
    contractNumber: string;
    contractDate: string;
    city: string;
    employerName: string;
    employerAddress: string;
    employerInn: string;
    employerKpp: string;
    employerOkpo: string;
    employerAccount: string;
    employerBank: string;
    employerBic: string;
    representative: string;
    representativePosition: string;
    representativeInitials: string;
    employee: string;
    employeeInitials: string;
    employeePassport: string;
    employeePassportIssuedBy: string;
    employeePassportIssuedAt: string;
    employeePassportCode: string;
    employeeRegistration: string;
    position: string;
    department: string;
    workPlace: string;
    workStart: string;
    workEnd: string;
    conditionsClass: string;
    salary: string;
    probationMonths: string;
    paidLeaveDays: string;
    workHoursFrom: string;
    workHoursTo: string;
    breakFrom: string;
    breakTo: string;
  }) => {
    const esc = (v: string | number | undefined) => (v === undefined || v === "" ? "—" : String(v));
    const block = (title: string, content: string) => `<div style="margin-bottom:10px;"><strong>${title}</strong> ${content}</div>`;

    return `<!doctype html>
<html lang="ru">
<head>
  <meta charset="utf-8" />
  <title>Трудовой договор № ${esc(data.contractNumber)}</title>
  <style>
    body { margin: 0; padding: 36px 40px; font-family: "Times New Roman", Times, serif; color: #000; }
    .wrap { max-width: 1160px; margin: 0 auto; font-size: 12px; line-height: 1.5; }
    .header { display: flex; justify-content: space-between; margin-bottom: 20px; font-size: 12px; }
    .title { text-align: center; font-size: 16px; font-weight: 700; margin-bottom: 6px; }
    .subtitle { text-align: center; font-size: 12px; margin-bottom: 18px; }
    .section-title { font-weight: 700; margin: 14px 0 8px; font-size: 13px; }
    .sign-row { display: flex; justify-content: space-between; margin-top: 24px; font-size: 12px; }
    .line { border-bottom: 1px solid #000; min-width: 220px; display: inline-block; }
    .muted { font-size: 10px; color: #555; }
    ol { padding-left: 18px; margin: 6px 0 12px; }
  </style>
</head>
<body>
  <div class="wrap">
    <div class="header">
      <div>Трудовой договор № <strong>${esc(data.contractNumber)}</strong></div>
      <div>${esc(data.city)} ${esc(data.contractDate)}</div>
    </div>

    <div class="title">ТРУДОВОЙ ДОГОВОР</div>
    <div class="subtitle">(общие условия заключения, права и обязанности сторон)</div>

    ${block("Работодатель:", `${esc(data.employerName)}, ${esc(data.employerAddress)}, ИНН ${esc(data.employerInn)}, КПП ${esc(data.employerKpp)}, ОКПО ${esc(data.employerOkpo)}. В лице ${esc(data.representativePosition)} ${esc(data.representative)}, действующего на основании ${esc(data.representativePosition ? "доверенности/устава" : "устава")}`)}
    ${block("Работник:", `${esc(data.employee)} (паспорт: ${esc(data.employeePassport)}, выдан ${esc(data.employeePassportIssuedBy)}, ${esc(data.employeePassportIssuedAt)}; код подразделения ${esc(data.employeePassportCode)}; адрес регистрации: ${esc(data.employeeRegistration)})`)}

    <div class="section-title">1. Предмет трудового договора</div>
    <ol>
      <li>Работодатель обязуется предоставить Работнику работу по должности ${esc(data.position)} в подразделении ${esc(data.department)}, место работы: ${esc(data.workPlace)}. Работник обязуется лично выполнять трудовые функции и соблюдать внутренний распорядок.</li>
      <li>Работа является основной. Класс условий труда: ${esc(data.conditionsClass)}.</li>
      <li>Работодатель обеспечивает условия труда и своевременную выплату заработной платы.</li>
    </ol>

    <div class="section-title">2. Срок действия договора</div>
    <ol>
      <li>Договор вступает в силу со дня его подписания: ${esc(data.contractDate)}.</li>
      <li>Дата начала работы: ${esc(data.workStart)}. Дата окончания работы: ${esc(data.workEnd)}.</li>
      <li>Испытательный срок: ${esc(data.probationMonths)} мес.</li>
    </ol>

    <div class="section-title">3. Оплата труда</div>
    <ol>
      <li>Должностной оклад: ${esc(data.salary)} руб. в месяц.</li>
      <li>Стимулирующие и компенсационные выплаты устанавливаются локальными актами Работодателя.</li>
      <li>Оплачиваемый отпуск: ${esc(data.paidLeaveDays)} календарных дней.</li>
    </ol>

    <div class="section-title">4. Режим рабочего времени и отдыха</div>
    <ol>
      <li>Рабочее время: с ${esc(data.workHoursFrom)} до ${esc(data.workHoursTo)}.</li>
      <li>Перерыв для отдыха и питания: с ${esc(data.breakFrom)} до ${esc(data.breakTo)}.</li>
    </ol>

    <div class="section-title">5. Реквизиты Работодателя</div>
    <div>Адрес: ${esc(data.employerAddress)}</div>
    <div>ИНН: ${esc(data.employerInn)} &nbsp;&nbsp; КПП: ${esc(data.employerKpp)}</div>
    <div>Р/с: ${esc(data.employerAccount)} &nbsp;&nbsp; Банк: ${esc(data.employerBank)} &nbsp;&nbsp; БИК: ${esc(data.employerBic)}</div>

    <div class="sign-row">
      <div>
        <div>Работодатель: <span class="line"></span></div>
        <div class="muted">(подпись, ${esc(data.representativeInitials)})</div>
      </div>
      <div>
        <div>Работник: <span class="line"></span></div>
        <div class="muted">(подпись, ${esc(data.employeeInitials)})</div>
      </div>
    </div>
    <div style="margin-top:12px; font-size:11px;">Экземпляр договора получил: _____________________ / ${esc(data.employeeInitials)}</div>
  </div>
</body>
</html>`;
  };

  const handlePrint = async (row: ContractRow) => {
    try {
      const [contract, orgs, individuals, accounts] = await Promise.all([
        apiGet<any>(`/api/contracts/${row.id}`),
        apiGet<Org[]>("/api/organizations"),
        apiGet<Individual[]>("/api/individuals"),
        apiGet<Account[]>("/api/accounts"),
      ]);

      const orgMap = new Map<number, Org>();
      (orgs ?? []).forEach((o) => orgMap.set(o.id, o));
      const indMap = new Map<number, Individual>();
      (individuals ?? []).forEach((i) => indMap.set(i.id, i));
      const accMap = new Map<number, Account>();
      (accounts ?? []).forEach((a) => accMap.set(a.id, a));

      const org = contract?.organization_id ? orgMap.get(contract.organization_id) : undefined;
      const repr = contract?.representative_id ? indMap.get(contract.representative_id) : undefined;
      const employee = contract?.employee_id ? indMap.get(contract.employee_id) : undefined;
      const account = org?.account_id ? accMap.get(org.account_id) : undefined;

      const reprName = repr ? [repr.last_name, repr.first_name, repr.patronymic].filter(Boolean).join(" ") : (contract?.representative_name ?? row.representative ?? "");
      const employeeName = employee ? [employee.last_name, employee.first_name, employee.patronymic].filter(Boolean).join(" ") : (contract?.employee_name ?? row.employee ?? "");
      const employeePassport = employee
        ? [employee.passport_series, employee.passport_number].filter(Boolean).join(" ")
        : (contract?.employee_passport_series && contract?.employee_passport_number
            ? `${contract.employee_passport_series} ${contract.employee_passport_number}`
            : row.employeePassport ?? "");

      const printable = {
        contractNumber: contract?.number ?? row.number,
        contractDate: asHumanDate(contract?.conclusion_date ?? row.conclusionDate),
        city: contract?.city ?? row.city ?? "",
        employerName: org?.title ?? contract?.organization_name ?? row.organization ?? "",
        employerAddress: org?.address ?? contract?.organization_address ?? "",
        employerInn: org?.inn ?? contract?.organization_inn ?? "",
        employerKpp: org?.kpp ?? contract?.organization_kpp ?? "",
        employerOkpo: org?.okpo ?? "",
        employerAccount: account?.number ?? "",
        employerBank: account?.bank ?? "",
        employerBic: account?.bic ?? "",
        representative: reprName,
        representativePosition: contract?.representative_position ?? row.representativePosition ?? "",
        representativeInitials: initials(reprName),
        employee: employeeName,
        employeeInitials: initials(employeeName),
        employeePassport,
        employeePassportIssuedBy: employee?.passport_issued_by ?? contract?.employee_passport_issued_by ?? "",
        employeePassportIssuedAt: asHumanDate(employee?.passport_issued_at ?? contract?.employee_passport_issued_at ?? ""),
        employeePassportCode: employee?.passport_division_code ?? contract?.employee_passport_division_code ?? "",
        employeeRegistration: employee?.registration_address ?? contract?.employee_registration_address ?? "",
        position: contract?.position_title ?? row.position ?? "",
        department: contract?.department_title ?? row.department ?? "",
        workPlace: contract?.place ?? row.place ?? "",
        workStart: asIso(contract?.work_date_from ?? row.workDateFrom),
        workEnd: asIso(contract?.work_date_to ?? row.workDateTo),
        conditionsClass: contract?.conditions_class ? String(contract.conditions_class) : (row.conditionsClass ?? ""),
        salary: contract?.salary ? String(contract.salary) : row.salary ?? "",
        probationMonths: contract?.probation_months ? String(contract.probation_months) : row.probationMonths ?? "",
        paidLeaveDays: contract?.paid_leave_days ? String(contract.paid_leave_days) : row.paidLeaveDays ?? "",
        workHoursFrom: contract?.work_hours_from ?? row.workHoursFrom ?? "",
        workHoursTo: contract?.work_hours_to ?? row.workHoursTo ?? "",
        breakFrom: contract?.break_from ?? row.breakFrom ?? "",
        breakTo: contract?.break_to ?? row.breakTo ?? "",
      } as const;

      const html = buildPrintHtml(printable);
      const blob = new Blob([html], { type: "text/html" });
      const url = URL.createObjectURL(blob);
      const win = window.open(url, "_blank", "noopener,noreferrer,width=1200,height=900");
      if (!win) {
        URL.revokeObjectURL(url);
        return;
      }
      setTimeout(() => URL.revokeObjectURL(url), 1000);
    } catch (err) {
      console.error("Failed to prepare contract print", err);
    }
  };

  const handleDelete = async (row: ContractRow) => {
    try {
      await apiDelete(`/api/contracts/${row.id}`);
    } catch (err) {
      console.error("Failed to delete contract", err);
    }
    setRows((prev) => prev.filter((item) => item.id !== row.id));
  };

  return (
    <div className="relative">
      <style jsx global>{`
        @media print {
          body { background: #fff; }
          .no-print { display: none !important; }
          .print-area { display: block !important; }
        }
        .print-area { display: none; }
      `}</style>
      <div className="no-print">
        <ListPage
          title="Трудовые договоры"
          description="Номера договоров, даты заключения, организация и сотрудник"
          columns={[
            { key: "number", label: "Номер" },
            { key: "conclusionDate", label: "Дата заключения" },
            { key: "organization", label: "Организация" },
            { key: "employee", label: "Сотрудник" },
          ]}
          rows={rows}
          createHref="/contracts/new"
          actions={{
            showEdit: true,
            showDelete: true,
            showPrint: true,
            onPrint: handlePrint,
            editHref: (row) => `/contracts/${row.id}/edit`,
            onDelete: (row) => handleDelete(row as ContractRow),
          }}
        />
      </div>

      <div className="print-area absolute inset-0 p-8 text-black bg-white">
        <div className="max-w-6xl mx-auto space-y-6">
          <div className="flex justify-between text-sm">
            <div>Трудовой договор № <span className="font-semibold">{fmt(printable.number)}</span></div>
            <div>{fmt(printable.city)} {fmt(printable.conclusionDate)}</div>
          </div>

          <div className="text-sm space-y-2">
            <div><strong>Работодатель:</strong> {fmt(printable.organization)}, в лице {fmt(printable.representativePosition)} {fmt(printable.representative)}, действующего на основании {fmt(printable.basis)}</div>
            <div><strong>Работник:</strong> {fmt(printable.employee)} ({fmt(printable.employeePassport)})</div>
            <div><strong>Стороны</strong> заключили настоящий договор о нижеследующем:</div>
          </div>

          <div className="space-y-2 text-sm">
            <div className="font-semibold">1. Предмет трудового договора</div>
            <div>1.1. Работодатель предоставляет работу по должности {fmt(printable.position)} в подразделении {fmt(printable.department)}, место работы: {fmt(printable.place)}.</div>
            <div>1.2. Работа является основной. Условия труда: класс {fmt(printable.conditionsClass)}.</div>
            <div>1.3. Работник подчиняется непосредственному руководителю подразделения.</div>
          </div>

          <div className="space-y-2 text-sm">
            <div className="font-semibold">2. Срок действия договора</div>
            <div>2.1. Договор вступает в силу со дня заключения. Дата начала работы: {fmt(printable.workDateFrom)}. Дата окончания (если срочный): {fmt(printable.workDateTo)}.</div>
            <div>2.2. Договор {printable.workDateTo ? "срочный" : "бессрочный"}. Испытательный срок: {fmt(printable.probationMonths)} мес.</div>
          </div>

          <div className="space-y-2 text-sm">
            <div className="font-semibold">3. Оплата труда</div>
            <div>3.1. Должностной оклад: {fmt(printable.salary)} руб. в месяц. Оплачиваемый отпуск: {fmt(printable.paidLeaveDays)} дн.</div>
            <div>3.2. Стимулирующие и компенсационные выплаты устанавливаются локальными актами Работодателя.</div>
          </div>

          <div className="space-y-2 text-sm">
            <div className="font-semibold">4. Режим труда и отдыха</div>
            <div>4.1. Рабочее время: с {fmt(printable.workHoursFrom)} до {fmt(printable.workHoursTo)}, перерыв с {fmt(printable.breakFrom)} до {fmt(printable.breakTo)}.</div>
          </div>

          <div className="space-y-2 text-sm">
            <div className="font-semibold">5. Подписи сторон</div>
            <div className="flex justify-between pt-2">
              <div>
                Работодатель: ________________________
                <div className="text-xs text-gray-600">подпись / Ф.И.О.</div>
              </div>
              <div>
                Работник: ____________________________
                <div className="text-xs text-gray-600">подпись / Ф.И.О.</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
